.chat {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: white;
}

.chat__header {
  padding: 12px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid lightgray;
  background-color: #f0f2f5;
}

.chat__header .MuiSvgIcon-root {
  cursor: pointer;
}

.chat__header>h4 {
  font-weight: 500;
  color: gray;
  text-align: center;
}
.chat__header>h3{
   padding-right: 120px;
   color: #FF8FB1;
}

.chat__name {
  color: black;
}

.chat__input {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid lightgray;
  background-color: #f0f2f5;
}

.chat__input>form {
  flex: 1;
}

.chat__input>form>input {
  width: 98%;
  outline: 0;
  border: 1px solid lightgray;
  border-radius: 999px;
  padding: 5px 0px 5px 15px;
}

.chat__input>form button {
  display: none;
}

.chat__messages {
  flex: 1;
  overflow-y: auto; /*so that when there is lot of message then it siply scroll and not ger overflwo*/
  background-image: url("https://www.toptal.com/designers/subtlepatterns/uploads/circuit.png");
}

.chat__messages::-webkit-scrollbar {
  width: 8px;
}

/* Handle */
.chat__messages::-webkit-scrollbar-thumb {
  background: lightgray;
  border-radius: 8px;
}

@media(max-width:600px) {
  .chat {
    flex: 1;
  }
}